.headerContainer {
  color: #ffffff;
  height: auto;
  background-color: #060d17;
  position: relative;
  z-index: 2;
}

.submenuContainer {
  padding: 1em 4em;
  display: flex;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  height: 100%;
}

.Left {
  display: flex;
  align-items: center;
}
.Left div {
  margin-right: 0.3em;
}

.logo img {
  height: 30px;
  cursor: pointer;
}

.MenuContainer {
  margin-left: 20px;
}

.MenuContainer ul {
  list-style: none;
  margin: 0;
  display: flex;
}

.Middle {
  width: 100%;
  margin-right: 30px;
  position: relative;
}

.MenuContainer ul li {
  font-size: 16px;
  margin-right: 1em;
  cursor: pointer;
}

.active,
ul li:hover {
  color: #ffffff;
}
.ProfileDetails {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.Menuicon {
  cursor: pointer;
}

.ProfileDetails button {
  color: #ffffff;
  padding: 7px 12px;
  border-radius: 0.2em;
  margin-right: 5px;
  font-size: 16px;
  border: none;
  outline: none;
  background-color: #1c252f;
}

.ProfileDetails button:hover {
  background-color: #394047;
  cursor: pointer;
}

@media screen and (max-width: 480px) {
  .submenuContainer {
    display: block;
  }

  .Left {
    display: block;
  }

  .Left div {
    display: flex;
    justify-content: center;
    align-content: center;
  }

  .MenuContainer {
    display: none;
  }

  .logo img {
    height: 40px;
    margin: 10px 0;
  }

  .Middle {
    width: 100%;
    margin: 10px 0;
  }

  .submenuContainer {
    padding: 0.5em 1em;
  }

  .ProfileDetails {
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
  }
}
